.sender {
	display: flex;
	padding: 20px;
	border-top: 1px solid var(--border);
  }
  
  .sender input {
	width: 100%;
	height: 100%;
	border: 1px solid var(--border);
	background-color: var(--accent-1);
	padding: 1rem 1.2rem;
	transition: border-color 200ms;
	border-radius: 5px;
	outline: none;
	color: var(--color);
  }
  
  .sender input:focus {
	border-color: var(--primary);
  }
  
  .sender button {
	height: 100%;
	padding: 1rem 1.2rem;
	border-radius: 5px;
	cursor: pointer;
	margin-left: 1rem;
	outline: none;
  
	border: 1px solid var(--primary);
	background: var(--primary);
	color: var(--primary-color);
  }